.option-list {
    position: absolute;
    top: 0;
    right: 0;
    width: 23rem;
    padding: 0.8rem 0;
    background-color: @color-white;
    text-align: left;
    box-shadow: @box-shadow;
    visibility: hidden;
    opacity: 0;
    transition: visibility @trans-fast, opacity @trans-fast;

    &.___is-visible {
        visibility: visible;
        opacity: 1;
    }

    li {
        padding: 0.4rem 2rem;
        line-height: @line-height--base;
        color: @color-black;
        font-weight: normal;
        transition: color @trans-fast;

        &:hover {
            color: @color-secondary;
        }
    }
}
