.card-tile-container {
    display: flex; // IE10-11 COLUMN FIX

    .___fill {
        height: 100%; // fill to row height
    }

    width: 100%;
}

.card-tile {
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: @color-white;
    box-shadow: @box-shadow;
    margin-bottom: 2rem;
    text-decoration: none;
    color: inherit;
    width: 100%; // IE10-11 COLUMN FIX

    .read-more {
        margin-top: 1.8rem;
    }

    &[href=""] {
        color: inherit;
        pointer-events: none;
        color: @color-grey--60;
        background: @color-grey--40;
    }

    &.___no-margin-bottom {
        margin-bottom: 0;
    }

    @media ( max-width: @screen-xs-max ) {
        &.___no-image {
            .read-more__circle {
                background-color: @color-white;
            }
            .card-tile__title,
            .read-more {
                color: @color-white;
            }

            &.card-tile:hover {
                .read-more__circle {
                    background-color: @color-quaternary;

                    &:before {
                        color: @color-white;
                    }
                }
            }

            &.___leren {
                background-color: @color-leren;

                .read-more__circle:before {
                    color: @color-leren;
                }
            }
            &.___vernieuwing {
                background-color: @color-vernieuwing;

                .read-more__circle:before {
                    color: @color-vernieuwing;
                }
            }
            &.___arbeidsvoorwaarden {
                background-color: @color-arbeidsvoorwaarden;

                .read-more__circle:before {
                    color: @color-arbeidsvoorwaarden;
                }
            }
            &.___wetten {
                background-color: @color-wetten;

                .read-more__circle:before {
                    color: @color-wetten;
                }
            }
            &.___actualiteit {
                background-color: @color-actualiteit;

                .read-more__circle:before {
                    color: @color-actualiteit;
                }
            }
            &.___overig {
                background-color: @color-overig;

                .read-more__circle:before {
                    color: @color-overig;
                }
            }
            &.___klas {
                background-color: @color-klas;

                .read-more__circle:before {
                    color: @color-klas;
                }
            }
        }
    }

    @media ( min-width: @screen-sm ) {
        min-height: 35.5rem;

        &.___small-card {
            min-height: 27.5rem;
        }

        &.homepage-block {
            min-height: 16rem;
        }

        &.navigation-block {
            min-height: 27.4rem;
        }
    }

    &.___blue,
    &.___green {
        color: @color-white;

        .card-tile__title {
            .font(title-small);
        }

        .read-more__circle {
            background-color: @color-white;
        }

        &:hover {
            .card-tile__title {
                color: @color-white;
            }
        }
    }

    &.___blue {
        background-color: @color-primary;

        .read-more__circle:before {
            color: @color-primary;
        }
    }

    &.___contact {
        &::before {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            font-size: 10rem;
            color: @color-tertiary;
            opacity: .5;
            z-index: 0;
        }
    }
}

// Full cover image
.card-tile.___full-image {
    @media ( max-width: @screen-xs-max ) {
        min-height: 9rem;

        &.___featured {
            min-height: 25rem;

            .card-tile__title {
                .font(title-small);
                padding: 0;
            }
        }
    }

    .card-tile__image {
        display: flex;
        flex-grow: 1;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            display: block;
            background: fade(@color-grey--80, 25%);
            background: linear-gradient(to right, fade(@color-grey--80, 40%) 0%, fade(@color-grey--80, 0%) 100%);
        }
    }

    .card-tile__title {
        color: @color-white;
        text-shadow: @text-shadow;

        &:hover {
            color: @color-white;
        }

        @media ( min-width: @screen-sm ) {
            padding: 0 6rem 0 0;
            .font(title-extra-large);
        }
    }

    .read-more {
        color: @color-white;
    }

    @media ( max-width: @screen-xs-max ) {
        .___search-results & {
            min-height: 9rem;

            .card-tile__title {
                max-width: ~"calc(100% - 3rem)";
                .font(medium);
            }

            .read-more__circle {
                margin: 0;
                position: absolute;
                right: 2rem;
                bottom: 2rem;
            }

            .read-more span {
                display: none;
            }
        }
    }

    &.___no-image {
        &.___leren,
        &.___vernieuwing,
        &.___arbeidsvoorwaarden,
        &.___wetten,
        &.___actualiteit,
        &.___overig,
        &.___klas {
            &:after {
                flex-shrink: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 15rem;
                margin: 0 4rem 0 0;
                color: fade(@color-black, 40%);

                @media ( max-width: @screen-xs-max ) {
                    display: none;
                }
            }
        }

        .read-more__circle {
            background-color: @color-white;
        }
        .card-tile__title,
        .read-more {
            text-shadow: none;
            color: @color-white;
        }

        &.card-tile:hover {
            .read-more__circle {
                background-color: @color-quaternary;

                &:before {
                    color: @color-white;
                }
            }
        }

        &.___leren {
            background-color: @color-leren;

            .read-more__circle:before {
                color: @color-leren;
            }

            .icon(cat-leren, after);
            &:after {
                font-size: 15rem;
            }
        }
        &.___vernieuwing {
            background-color: @color-vernieuwing;

            .read-more__circle:before {
                color: @color-vernieuwing;
            }

            .icon(cat-vernieuwing, after);
            &:after {
                font-size: 14rem;
            }
        }
        &.___arbeidsvoorwaarden {
            background-color: @color-arbeidsvoorwaarden;

            .read-more__circle:before {
                color: @color-arbeidsvoorwaarden;
            }

            .icon(cat-arbeidsvoorwaarden, after);
            &:after {
                font-size: 14rem;
            }
        }
        &.___wetten {
            background-color: @color-wetten;

            .read-more__circle:before {
                color: @color-wetten;
            }

            .icon(cat-wetten-2, after);
            &:after {
                font-size: 13.4rem;
            }
        }
        &.___actualiteit {
            background-color: @color-actualiteit;

            .read-more__circle:before {
                color: @color-actualiteit;
            }

            .icon(cat-actualiteit, after);
            &:after {
                font-size: 13.6rem;
            }
        }
        &.___overig {
            background-color: @color-overig;

            .read-more__circle:before {
                color: @color-overig;
            }

            .icon(cat-overig, after);
            &:after {
                font-size: 13rem;
            }
        }
        &.___klas {
            background-color: @color-klas;

            .read-more__circle:before {
                color: @color-klas;
            }

            .icon(cat-klas, after);
            &:after {
                font-size: 13rem;
            }
        }

        @media ( min-width: @screen-sm ) {
            flex-direction: row;
        }
    }
}

.card-tile__image {
    position: relative;
    width: 100%;
    min-height: 12rem;
    background-size: cover;
    background-position: center;

    @media ( max-width: @screen-xs-max ) {
        min-height: 9rem;
    }

    .card-tile.___leren &,
    .card-tile.___vernieuwing &,
    .card-tile.___arbeidsvoorwaarden &,
    .card-tile.___wetten &,
    .card-tile.___actualiteit &,
    .card-tile.___overig &,
    .card-tile.___klas & {
        &:after {
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            width: 4.6rem;
            height: 4.6rem;
            line-height: inherit;
            background-color: fade(@color-black, 40%);
            top: 3rem;
            right: 3rem;
            border-radius: 50%;
            color: @color-white;
            text-align: center;
        }
    }

    .card-tile.___leren & {
        .icon(cat-leren, after);
        &:after {
            font-size: 3rem;
        }
    }
    .card-tile.___vernieuwing & {
        .icon(cat-vernieuwing, after);
        &:after {
            font-size: 3.1rem;
        }
    }
    .card-tile.___arbeidsvoorwaarden & {
        .icon(cat-arbeidsvoorwaarden, after);
        &:after {
            font-size: 3rem;
        }
    }
    .card-tile.___wetten & {
        .icon(cat-wetten, after);
        &:after {
            font-size: 2.6rem;
        }
    }
    .card-tile.___actualiteit & {
        .icon(cat-actualiteit, after);
        &:after {
            font-size: 2.8rem;
        }
    }
    .card-tile.___overig & {
        .icon(cat-overig, after);
        &:after {
            font-size: 2.5rem;
        }
    }
    .card-tile.___klas & {
        .icon(cat-klas, after);
        &:after {
            font-size: 2.7rem;
        }
    }

    @media ( max-width: @screen-xs-max ) {
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            display: block;
            background: fade(@color-grey--80, 25%);
            background: linear-gradient(to right, fade(@color-grey--80, 40%) 0%, fade(@color-grey--80, 0%) 100%);
        }

        &:after {
            opacity: 0;
            visibility: none;
        }
    }
}

.card-tile__placeholder {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 12rem;

    &:after {
        color: fade(@color-black, 40%);
    }

    @media ( max-width: @screen-xs-max ) {
        min-height: 9rem;

        &:after {
            opacity: 0;
            visibility: none;
        }
    }

    .card-tile.___leren & {
        background-color: @color-leren;

        .icon(cat-leren, after);
        &:after {
            font-size: 7.2rem;
        }
    }

    .card-tile.___vernieuwing & {
        background-color: @color-vernieuwing;

        .icon(cat-vernieuwing, after);
        &:after {
            font-size: 7.2rem;
        }
    }

    .card-tile.___arbeidsvoorwaarden & {
        background-color: @color-arbeidsvoorwaarden;

        .icon(cat-arbeidsvoorwaarden, after);
        &:after {
            font-size: 7rem;
        }
    }

    .card-tile.___wetten & {
        background-color: @color-wetten;

        .icon(cat-wetten, after);
        &:after {
            font-size: 6rem;
        }
    }

    .card-tile.___actualiteit & {
        background-color: @color-actualiteit;

        .icon(cat-actualiteit, after);
        &:after {
            font-size: 6.4rem;
        }
    }

    .card-tile.___overig & {
        background-color: @color-overig;

        .icon(cat-overig, after);
        &:after {
            font-size: 6rem;
        }
    }

    .card-tile.___klas & {
        background-color: @color-klas;

        .icon(cat-klas, after);
        &:after {
            font-size: 6.2rem;
        }
    }
}

.card-tile__content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
    width: 100%;
    padding: 2.5rem 3rem 3rem;
    z-index: 0;  // stay on top of grey background gradient
    position: relative;

    @media ( max-width: @screen-xs-max ) {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 2rem;
    }

    p {
        margin-bottom: 0;
        .ellipsis(@line-height--base, 4);

        @media ( max-width: @screen-xs-max ) {
            display: none;
        }
    }
}

.card-tile__title {
    font-size: @font-size--title-small;
    line-height: @line-height--title-small;
    margin: 0 0 1rem 0;

    @media ( max-width: @screen-xs-max ) {
        padding: 0 4rem 0 0;
        .font(medium);
        color: @color-white;
        text-shadow: @text-shadow;

        .card-tile.___no-image & {
            text-shadow: none;
        }
    }

    .___small-card & {
        margin: 0;
    }
}

.card-tile__subtitle {
    .font(base);
    color: @color-primary;
    margin: 0 0 1rem 0;
}

.card-tile__small-text {
    .font(medium);
}

.card-tile__icon {
    display: block;
    position: relative;
    min-height: 10rem;
    text-align: center;
    color: @color-primary;
    font-size: 8rem;
    transition: color @trans-fast;

    [href=""] > & {
        color: @color-grey--50;
    }

    .card-tile:hover & {
        color: @color-tertiary;
    }

    &.___positioned-right {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        right: 4.5rem;
        bottom: 0;
        font-size: 6.5rem;
    }

    &.___large {
        font-size: 8rem;
    }

    &.___nudge-to-right {
        margin-right: -1.5rem;
    }
}

.card-tile__link {
    display: block;
    margin: -(@calc-line-height/2) 0 -(@calc-line-height/2);
    text-decoration: none;
    color: @color-black;
    transition: color @trans-fast;

    [href=""] > & {
        color: transparent;
    }

    .card-tile:hover & {
        color: @color-primary;
    }
}

.card-tile__link__icon {
    padding-right: 0.8rem;
    font-size: 1.1rem;
    position: relative;
    top: -0.1rem;
}
